import React, { memo, useMemo } from 'react';
import { cssDecorator } from '@front-toolkit/utils';
import { TabsProps, TabItem, TabsWidth } from 'core/types/tabs';

import Tab, { tabsElementIdToIdentify } from './Tab';

import styles from './styles.scss';

const cd = cssDecorator(styles);

const Tabs = memo<TabsProps>(({
  activeValue,
  items,
  onChange,
  width = TabsWidth.Hug,
  level = 'level1',
  gap = 'default',
  dataTestId = 'tabs_id'
}) => {
  const activeTabItem = useMemo(
    () => items.find(({ value }) => value === activeValue),
    [activeValue, items]
  );
  const activeTab = activeTabItem && !activeTabItem.disabled ? activeTabItem.value : '';

  const isHug = width === TabsWidth.Hug;

  const styleTabs = {
    gridTemplateColumns: `repeat(${ items.length }, ${ isHug ? 'auto' : '1fr' })`
  };

  const onClick = (
    event: React.MouseEvent<HTMLButtonElement>,
    value: string,
    disabled: boolean
  ): void => {
    if (disabled) return;

    const target = event.target as Element;

    if (!target.closest(`#${ tabsElementIdToIdentify }`)) {
      onChange(value);
    }
  };

  return (
    <nav
      className={ cd('tabs', { hug: isHug }) }
      data-test-id={ dataTestId }
      style={ styleTabs }
    >
      { items.map((item: TabItem) => React.createElement(
        Tab,
        {
          ...item,
          onClick,
          gap,
          key: item.value,
          width,
          isActive: activeTab === item.value,
          level
        }
      )) }
    </nav>
  );
});

Tabs.displayName = 'Tabs';

export { Tabs };
